.social-link {
  color: var(--main-pre-title-color);
  letter-spacing: 2.2px;
  font-size: 11px;
  font-weight: 600;
  line-height: normal;
  transition: all .2s linear;
}

.social-link:hover, .social-link:focus {
  color: var(--accent-color);
  outline: none;
}

.social-link:after {
  content: "";
  visibility: hidden;
  height: 1px;
  width: 100%;
  background-color: var(--accent-color);
  border-radius: 4px;
  transition: all .18s linear;
  display: block;
  transform: scale(0);
}

.social-link:hover:after, .social-link:focus:after {
  visibility: visible;
  transform: scale(1);
}

.btn {
  min-width: 160px;
  height: 40px;
  letter-spacing: 1.08px;
  border: 1px solid var(--accent-color);
  text-transform: uppercase;
  color: var(--main-btn-color);
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border-radius: 25px;
  font-family: inherit;
  font-size: 12px;
  font-weight: 600;
  line-height: 1.33;
  transition: all .15s linear;
  display: block;
}

.btn:hover, .btn:focus {
  background-color: var(--btn-hover);
}

.pre-title {
  font-size: var(--pre-title-font-size);
  color: var(--main-pre-title-color);
  text-transform: uppercase;
  align-items: center;
  font-weight: 600;
  line-height: 1.36;
  display: inline-flex;
}

.pre-title:before {
  content: " ";
  width: 60px;
  border-bottom: 1px solid;
  margin-right: 20px;
}

.section-header {
  font-family: Raleway, sans-serif;
  font-size: var(--title-font-size);
  color: var(--second-title-color);
  letter-spacing: 2.1px;
  font-weight: 700;
  line-height: 1.33;
}

.sub-title {
  font-size: var(--sub-title-font-size);
  color: var(--main-sub-title-color);
  letter-spacing: .36px;
  font-weight: 400;
  line-height: 30px;
}

.text-description {
  color: var(--main-sub-title-color);
  letter-spacing: .28px;
  margin-bottom: 38px;
  font-size: 14px;
  line-height: 1.85714;
}

.modal-backdrop {
  z-index: 99;
  width: 100%;
  height: 100%;
  background-color: rgba(9, 11, 19, .345);
  transition: opacity .2s linear, visibility .2s linear;
  position: fixed;
  top: 0;
}

.modal-window {
  width: 528px;
  min-height: 600px;
  background-color: var(--main-bg-color);
  border-radius: 5px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

@media screen and (min-width: 320px) and (max-width: 767px) {
  .modal-window {
    width: 95vw;
  }
}

.modal-window-close-btn {
  width: 30px;
  height: 30px;
  background-color: var(--main-bg-color);
  color: var(--feature-text-color);
  cursor: pointer;
  border: 1px solid rgba(0, 0, 0, .29);
  border-radius: 50px;
  padding: 6px;
  transition: color .2s linear;
  position: absolute;
  top: 5px;
  right: 5px;
}

.modal-window-close-btn:hover {
  color: var(--accent-color);
}

.modal-btn-close-icon {
  fill: currentColor;
}

.modal-form {
